'use client';

import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { motion } from 'framer-motion';

import { Button } from '../elements/button';
import { Heading } from '../elements/heading';
import { Subheading } from '../elements/subheading';

/* ──────────────────────────────────────────────────────────────
  Strapi helpers
────────────────────────────────────────────────────────────── */
const STRAPI_BASE = (
  process.env.NEXT_PUBLIC_STRAPI_URL ??
  process.env.NEXT_PUBLIC_API_URL ??
  ''
).replace(/\/$/, '');

const toAbs = (u?: string | null) => {
  if (!u) return '';
  if (u.startsWith('http')) return u;
  return STRAPI_BASE ? `${STRAPI_BASE}${u}` : u;
};

/* ──────────────────────────────────────────────────────────────
  Types
────────────────────────────────────────────────────────────── */
type CTA = {
  id: number;
  text: string;
  URL: string;
  target?: string | null; // pl. "_blank"
  variant?: 'primary' | 'outline' | 'muted' | 'simple' | string;
};

type StrapiImageFormat = {
  url?: string | null;
  width?: number | null;
  height?: number | null;
};

type StrapiMediaImage = {
  id: number;
  url?: string | null;
  alternativeText?: string | null;
  formats?: {
    thumbnail?: StrapiImageFormat;
    small?: StrapiImageFormat;
    medium?: StrapiImageFormat;
    large?: StrapiImageFormat;
    [key: string]: StrapiImageFormat | undefined;
  } | null;
};

type Props = {
  heading: string;
  sub_heading: string;
  CTAs: CTA[];
  locale: string;
  image?: StrapiMediaImage | null;
};

export const Hero = ({ heading, sub_heading, CTAs, locale, image }: Props) => {
  const preferred =
    image?.formats?.large?.url ||
    image?.formats?.medium?.url ||
    image?.formats?.small?.url ||
    image?.url ||
    null;

  const imgSrc = toAbs(preferred);
  const hasImage = Boolean(imgSrc);
  const imgAlt = image?.alternativeText || heading || 'Hero image';

  const withLocale = (url: string) => {
    const path = url?.startsWith('/') ? url : `/${url || ''}`;
    return `/${locale}${path}`.replace(/\/{2,}/g, '/');
  };
  const toButtonVariant = (v?: string | null): 'simple' | 'outline' | 'primary' | 'muted' => {
    return v === 'simple' || v === 'outline' || v === 'primary' || v === 'muted'
      ? v
      : 'primary';
  };


  return (
    <section className="relative isolate min-h-[100svh] overflow-hidden bg-black">
      {/* FULL-BLEED background image (1x) */}
      {hasImage && (
        <Image
          src={imgSrc}
          alt={imgAlt}
          fill
          priority
          sizes="100vw"
          className="object-cover grayscale saturate-0 contrast-125 brightness-[0.72]"
        />
      )}

      {/* Neutral overlays ONLY (no teal => no kék) */}
      <div className="absolute inset-0 bg-gradient-to-b from-black/70 via-black/35 to-black/75" />
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_25%_20%,rgba(255,255,255,0.10),transparent_60%)]" />

      {/* Content */}
      <div className="relative z-10 mx-auto flex min-h-[100svh] max-w-7xl items-center px-4 py-16 md:px-8">
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.55, ease: [0.22, 1, 0.36, 1] }}
          className="w-full"
        >
          <div className="max-w-3xl">
            <Heading
              as="h1"
              className="text-left text-4xl font-semibold leading-[1.05] text-white md:text-5xl lg:text-7xl"
            >
              {heading}
            </Heading>

            <Subheading className="mt-4 text-left text-base text-white/80 md:mt-6 md:text-xl">
              {sub_heading}
            </Subheading>

            <div className="mt-8 flex flex-wrap gap-3">
              {CTAs?.map((cta) => {
                const href = withLocale(cta.URL);
                const target = cta.target || undefined;
                const rel = target === '_blank' ? 'noopener noreferrer' : undefined;

                return (
                  <Button key={cta.id} variant={toButtonVariant(cta.variant)} asChild>
                    <Link href={href} target={target} rel={rel}>
                      {cta.text}
                    </Link>
                  </Button>
                );
              })}
            </div>
          </div>
        </motion.div>
      </div>

      <div className="pointer-events-none absolute inset-x-0 bottom-0 h-40 bg-gradient-to-t from-black/45 to-transparent" />
    </section>
  );
};
